.header {
    .flex-center-between;
    grid-area: header;
    padding: 10px 20px;
    // Логотип
    .logo {
      a {
        font-size: 36px;
        font-weight: bold;
        text-decoration: none;
        color: @color-element-dark-primary;
        .transition;
        &:hover {
          color: lighten(@color-element-dark-secondary,10%);
        }
      }
    }
    .user {
      display: flex;
      align-items: center;
      &__name {
        font-weight: bold;
        margin-right: 40px;
      }
      &__login {
        display: flex;
        a {
          color: @color-text-gray;
          font-size: 0.875em;
          &::before {
            content: '';
            display: inline-block;
            width: 1px;
            height: 13px;
            margin: 0 10px;
            background: @color-text-gray;
          }
          &:first-child {
            &::before {
              display: none;
            }
          }
        }
      }
    }
}
