  grid-area: header;
  padding: 10px 20px;
}
.header .logo a {
  font-size: 36px;
  font-weight: bold;
  text-decoration: none;
  color: #433c56;
  transition: all 0.25s ease-out;
}
.header .logo a:hover {
  color: #685780;
}
.header .user {
  display: flex;
  align-items: center;
}
.header .user__name {
  font-weight: bold;
  margin-right: 40px;
}
.header .user__login {
  display: flex;
}
.header .user__login a {
  color: #7b7c81;
  font-size: 0.875em;
}
.header .user__login a::before {
  content: '';
  display: inline-block;
  width: 1px;
  height: 13px;
  margin: 0 10px;
  background: #7b7c81;
}
.header .user__login a:first-child::before {
  display: none;
}
.aside-menu {
  grid-area: aside;
  background: #433c56;
  border-radius: 12px 0 0 0;
}
.nav {
  position: sticky;
  top: 10px;
  padding: 0 10px;
  margin: 10px 0;
}
.nav__link {
  display: flex;
  align-items: center;
  padding: 10px;
  text-decoration: none;
  color: #8d83a8;
  transition: all 0.25s ease-out;
}
.nav__link:hover {
  color: #a8a1bd;
}
.nav__link:hover svg {
  fill: #a8a1bd;
}
.nav__link svg {
  height: 18px;
  width: 18px;
  margin-right: 10px;
  fill: #8d83a8;
  transition: all 0.25s ease-out;
}
.nav__item {
  list-style: none;
}
.nav__item:nth-child(2n) {
  margin: 5px 0;
}
.nav__item_current .nav__link {
  color: #ffffff;
  background: #8a7aaf;
  border-radius: 6px;
}
.nav__item_current .nav__link svg {
  fill: #ffffff;
}
#caros-is {
  height: 100%;
  max-width: 1140px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-template-rows: 70px auto;
  grid-template-areas: "header header header" "aside main main" "aside main main";
}
.content {
  display: grid;
  grid-template-columns: 1fr 340px;
  grid-gap: 20px;
  grid-area: main;
  padding: 20px;
  background: #f0f1f5;
