.toolbar:hover .toolbar__icon,
.toolbar:hover .toolbar__icon::before,
.toolbar:hover .toolbar__icon::after {
  background: #8a7aaf;
}
.toolbar:hover .toolbar__act {
  opacity: 1;
  height: auto;
  width: auto;
}
.toolbar:hover .toolbar__act li {
  opacity: 1;
  transition: all 0.25s ease-out;
}
