.toolbar__icon {
  position: relative;
  display: inline-block;
  width: 3px;
  height: 3px;
  background: #7b7c81;
  border-radius: 50%;
  transition: all 0.25s ease-out;
}
.toolbar__icon::before,
.toolbar__icon::after {
  content: '';
  position: relative;
  display: inline-block;
  width: 3px;
  height: 3px;
  background: #7b7c81;
  border-radius: 50%;
  transition: all 0.25s ease-out;
  position: absolute;
}
.toolbar__icon::before {
  top: 6px;
}
.toolbar__icon::after {
  bottom: 6px;
}
.toolbar__act {
  position: absolute;
  top: 0;
  right: 15px;
  margin: 0;
  background: #ffffff;
  box-shadow: 0 0 25px rgba(138, 122, 175, 0.25);
  z-index: 1;
  opacity: 0;
  overflow: hidden;
  height: 0;
  width: 0;
