.transition (@property: all, @duration: 0.25s, @function: ease-out ) {
    transition: @property @duration @function;
}

.radius {
  border-radius: @border-radius;
}

// Flex box

.flex-center-between{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-center-end{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.col {
  display: flex;
  flex-direction: column;
}
