// Ссылка на документ
.document__link {
  position: relative;
  .flex-center-between;
  &:nth-of-type(2n) {
    margin: 5px 0;
  }
  .link {
    color: @color-text-gray;
    margin-left: 5px;
  }
}

// Modules bg color
.module_primary {
  background: @color-element-white;
  box-shadow: 0 0 25px fadeout(@color-buttons,85%);
}
.module_secondary {
  background: @color-element-light-secondary;
  box-shadow: 0 0 25px fadeout(@color-buttons,95%);
}

// Modules default
.module {
  height: fit-content;
  .radius;
  // header
  &__header {
    .flex-center-between;
    padding: 20px;
    h5 {
      margin: 0;
    }
    span {
      font-size: 0.750em;
      &:not([class]) {
        color: @color-text-gray-light;
      }
      &.mark_user {
        padding: 2px 8px;
        color: @color-element-white;
        background: @color-forms-primary;
        border-radius: 999px;
      }
    }
  }
  // content
  &__content {
    padding: 0 20px;
    padding-bottom: 20px;
    .row,
    .row > .row__content {
      &:nth-of-type(2n) {
        margin: 15px 0;
      }
      & > span {
        font-size: 0.875em;
        margin-left: 40%;
        &:first-child {
          color: @color-text-gray;
          margin-left: 0;
        }
        &:nth-child(2) {
          margin-left: 0;
        }
      }
    }
    .row__content {
      display: flex;
      flex-grow: 1;
    }
    .col {
      &:nth-of-type(2n) {
        margin: 0 20px;
      }
      span {
        font-size: .875em;
      }
    }
    .col-xs {
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
      }
    }
    // Развернуть всю информацию
    .expand {
      margin: 15px 0;
      margin-bottom: 0;
      padding: 5px;
      text-align: center;
      background: @color-element-light-primary;
      border-radius: 6px;
      cursor: pointer;
      .transition;
      &:hover {
        background: @color-element-light-secondary;
      }
      span {
        display: inline-block;
        font-size: 0.750em;
      //  text-transform: uppercase;
        color: @color-text-gray;
        &::after {
          content: '';
          position: relative;
          top: 6px;
          border: 3px solid;
          border-bottom-width: 0;
          border-left-color: transparent;
          border-right-color: transparent;
          border-top-color: @color-text-gray;
          margin-left: 10px;
        }
      }
    }
  }
  // module__footer
  &__footer {
    .flex-center-end;
    padding: 20px;
    border-top: 1px solid @color-element-light-primary;
    span {
      &:nth-of-type(2n) {
        margin-left: 20px;
      }
    }
    //
    svg {
      position: relative;
      top: 2px;
      height: 14px;
      width: 14px;
      margin-right: 5px;
      fill: @color-element-dark-primary;
      .transition;
    }
    //
  }

  &-user,
  &-contacts {
    .row,
    .row > .row__content {
      & > span {
        width: 60%;
        &:first-child {
          width: 40%;
        }
      }
    }
  }
  // user
  &-user {
    .col {
      span {
        &:first-child {
          font-size: 0.6875em;
          color: @color-text-gray-light;
        }
      }
    }
    .row {
      .row {
        margin-left: 40%;
        &:first-of-type {
          margin-left: 0;
        }
      }
    }
  }
}

// комментарии

.comments {
  padding: 20px;
}

.comment {
  margin-bottom: 20px;
  &__header {
    .flex-center-between;
    margin-bottom: 10px;
    span {
      font-size: 0.750em;
      color: @color-text-gray-light;
    }
  }
  &__text {
    font-size: 0.750em;
    margin: 0;
  }
}
