.aside-menu {
    grid-area: aside;
    background: @color-element-dark-primary;
    border-radius: 12px 0 0 0;
}

.nav {
  position: sticky;
  top: 10px;
  padding: 0 10px;
  margin: 10px 0;
  // Ссылка
  &__link {
    display: flex;
    align-items: center;
    padding: 10px;
    text-decoration: none;
    color: lighten(@color-element-dark-primary, 30%);
    .transition;
    &:hover {
      color: lighten(@color-element-dark-primary, 40%);
      svg {
        fill: lighten(@color-element-dark-primary, 40%);
      }
    }
    svg {
      height: 18px;
      width: 18px;
      margin-right: 10px;
      fill: lighten(@color-element-dark-primary, 30%);
      .transition;
    }
  }
  // Элементы
  &__item {
    list-style: none;
    &:nth-child(2n) {
      margin: 5px 0;
    }
    &_current {
      .nav__link {
        color: @color-text-light;
        background: @color-buttons;
        .radius;
        svg {
          fill: @color-text-light;
        }
      }
    }
  }
}
