.btn{
  display: inline-block;
  vertical-align: top;
  font-size: 0.875em;
  text-align: center;
  text-decoration: none;
  color: @color-text-light;
  background: @color-buttons;
  padding: 15px;
  cursor: pointer;
  .radius;
  box-sizing: border-box;
  .transition;
  &:hover,
  &--hover{
    background: @color-buttons-hover;
  }

  &:active,
  &--active{

  }

  // Size modificator

  &-size {
    width: 100%;
  }

  &-auto {
    width: auto;
  }

  &[disabled] {
    background: @color-forms-primary;
  }

  // Color modificator
}
