#caros-is {
    height: 100%;
    max-width: 1140px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 200px 1fr;
    grid-template-rows: 70px auto;
    grid-template-areas:
        "header header header"
        "aside main main"
        "aside main main";
}

.content{
    display: grid;
    grid-template-columns: 1fr 340px;
    grid-gap: 20px;
    grid-area: main;
    padding: 20px;
    background: @color-element-light-primary;
    border-radius: 0 12px 0 0;
    &__nav {
      grid-column: span 2;
      a {
        font-size: .750em;
        &:hover {
          svg {
            fill:lighten(#413f73,5%);
          }
        }
      }
      svg {
        position: relative;
        top: 3px;
        height: 14px;
        width: 14px;
        margin-right: 5px;
        fill: @color-text-gray-light;
        .transition;
      }
    }
    &__main,
    &__aside{
      display: grid;
      grid-gap: 20px;
      grid-template-rows: fit-content(100%);
    }
}
