// Декоративные элементы

// метки для документов
.mark {
  font-size: 0.625em;
  padding: 2px 4px;
  padding-top: 4px;
  color: @color-text-light;
  user-select: none;
  &_pdf {
    background: #FC2130;
  }
  &_doc {
    background: #5B94D6;
  }
  &_xls {
    background: #55B74F;
  }
}

// тулбар
.toolbar {
  position: relative;
  padding: 0 6px;
  margin-left: -6px;
  cursor: pointer;
  &:hover {
    .toolbar__icon,
    .toolbar__icon::before,
    .toolbar__icon::after {
        background: @color-buttons;
    }
    .toolbar__act {
      opacity: 1;
      height: auto;
      width: auto;
      li {
        opacity: 1;
        .transition;
        &:hover {
          background: @color-element-light-primary;
        }
      }
    }
  }
}

.toolbar__icon {
  position: relative;
  display: inline-block;
  width: 3px;
  height: 3px;
  background: @color-text-gray;
  border-radius: 50%;
  .transition;
}

.toolbar__icon::before,
.toolbar__icon::after {
  content: '';
  .toolbar__icon;
  position: absolute;
}

.toolbar__icon::before {
  top: 6px;
}

.toolbar__icon::after {
  bottom: 6px;
}

.toolbar__act {
  position: absolute;
  top: 0;
  right: 15px;
  margin: 0;
  background: @color-element-white;
  box-shadow: 0 0 25px fadeout(@color-buttons,75%);
  z-index: 1;
  opacity: 0;
  overflow: hidden;
  height: 0;
  width: 0;
  padding: 5px 0;
  .radius;
  .transition;
  li {
    padding: 5px 15px;
    font-size: 0.750em;
    list-style-type: none;
    opacity: 0;
  }
}
